<h1 id="ssh" tabindex="-1"><a class="header-anchor" href="#ssh" aria-hidden="true">#</a> SSH</h1>
<h2 id="description" tabindex="-1"><a class="header-anchor" href="#description" aria-hidden="true">#</a> Description</h2>
<p>Les usages de base de SSH sont :</p>
<ul>
<li>Accéder à distance à la console en ligne commande (shell), ce qui permet, entre autres, d'effectuer la totalité des opérations courantes et/ou d'administration sur la machine distante.</li>
<li>Déporter l'affichage graphique de la machine distante.</li>
<li>Transferts de fichiers en ligne de commande.</li>
<li>Montage ponctuel de répertoires distants, soit en ligne de commande, soit via Nautilus, sous Gnome par exemple.</li>
<li>Montage automatique de répertoires distants.</li>
</ul>
<p><a href="https://doc.ubuntu-fr.org/ssh" target="_blank" rel="noopener noreferrer">Source<OutboundLink/></a></p>
<h2 id="utilisation" tabindex="-1"><a class="header-anchor" href="#utilisation" aria-hidden="true">#</a> Utilisation</h2>
<h3 id="connexion" tabindex="-1"><a class="header-anchor" href="#connexion" aria-hidden="true">#</a> Connexion</h3>
<p>Pour se connecter à distance il faut utiliser la commande suivante :</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">ssh</span> <span class="token operator">&lt;</span>nom_utilisateur<span class="token operator">></span>@<span class="token operator">&lt;</span>ipaddress<span class="token operator">></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>